/* Reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Arimo', sans-serif;
}


header img {
  width: 20px;
}

header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  min-height: 80px;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: black;
}

.hamburger {
  cursor: pointer;
  justify-self: flex-end;
}

.hamburger:hover {
  transform: scaleX(1.05);
}

.nav {
  background: #ffffffea;
  position: absolute;
  top: -100%;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  z-index: 99;
  transition: all 0.7s ease-in-out;
}

.nav-item a,
li {
  list-style: none;
  padding: 0 5px;
  font-size: 0.85rem;
  color: #303030;
}

.nav-item a:hover,
li:hover {
  color: #979797;
}

.nav-basket,
.menu-close-btn {
  cursor: pointer;
}

.profile {
  margin-right: 10px;
  width: 15px;
}

.profile-hidden {
  display: none;
}

.menu-close-btn {
  position: absolute;
  top: 4%;
  right: 4%;
}

.nav.toggle-menu {
  top: 0;
}

@media (min-width: 500px) {
  .hamburger,
  .menu-close-btn {
    display: none;
  }
  .nav {
    display: flex;
    flex-direction: row;
    position: static;
    height: 70px;
    transition: none;
  }
}
/*# sourceMappingURL=Nav.css.map */
.hero {
  background-image: url(/shop/static/media/hero.82c810ba.jpg);
  background-color: black;
  height: 70vh;
  width: 100%;
  position: relative;
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  margin: auto;
  margin-top: 50px;
}

.hero__overlay {
  height: 100%;
  width: 100%;
}

.hero__overlay:before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.849), transparent 100%);
}

.hero__title {
  width: 75%;
  color: white;
  z-index: 100;
  position: absolute;
  top: 60%;
  left: 5%;
}
/*# sourceMappingURL=Hero.css.map */
.featured {
  margin-top: 70px;
  margin-bottom: 70px;
}

.featured a {
  text-decoration: none;
  color: inherit;
}

.title {
  font-size: 1.1rem;
  text-align: center;
  font-weight: 500;
}

.info {
  font-size: 0.8rem;
  text-align: center;
  color: #6e6e6e;
}

.item-list {
  width: 90%;
  margin: 50px auto 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, 170px);
  grid-auto-rows: -webkit-max-content;
  grid-auto-rows: max-content;
  justify-content: center;
  align-content: center;
  grid-gap: 1%;
}

.item {
  padding: 5px;
  box-shadow: 0 0 2px #b3b3b3;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
}

.item:hover .item-title {
  color: #6e6e6e;
}

.item-image {
  max-width: 80%;
  max-height: 200px;
  display: block;
  margin: 0 auto;
  margin-top: 10px;
}

.item-details {
  border-top: 1px solid black;
  width: 95%;
  margin: auto;
  margin-top: 10px;
  margin-bottom: 10px;
}

.item-title {
  font-size: 0.9rem;
  font-weight: 300;
}

.item-price {
  margin: 5px auto;
  color: #444444;
}
/*# sourceMappingURL=Featured.css.map */
@use '../Featured/Featured.scss';
.products {
  max-width: 1300px;
  display: block;
  margin: 70px auto;
  margin-bottom: 100px;
}

.products a {
  text-decoration: none;
  color: inherit;
}

.item-description {
  font-size: 0.9rem;
  width: 90%;
  max-width: 500px;
}

.add-btn {
  width: 75%;
  display: block;
  border: 1px solid #cecece;
  background: #ebebeb;
  font-size: 0.7rem;
  padding: 5px 10px;
  margin-top: 10px;
  cursor: pointer;
}

.add-btn:hover {
  background: #acacac;
}

.search-bar {
  width: 90%;
  max-width: 1300px;
  display: block;
  margin: 10px auto;
}

.search-input {
  box-shadow: 0 0 2px #b3b3b3;
  padding: 10px;
  border: none;
  display: block;
  margin: 0 2% 0 auto;
  outline: none;
}

.search-input:focus {
  box-shadow: 0 0 2px #444444;
}

.search-input::-webkit-input-placeholder {
  color: #b3b3b3;
  font-style: italic;
}

.search-input:-ms-input-placeholder {
  color: #b3b3b3;
  font-style: italic;
}

.search-input::placeholder {
  color: #b3b3b3;
  font-style: italic;
}

.categories-list {
  width: 90%;
  margin: 10px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.category {
  border: none;
  color: black;
  outline: none;
  background: transparent;
  padding: 5px 10px;
  margin: 2px;
  text-transform: uppercase;
  cursor: pointer;
}

.category:hover {
  border-bottom: 1px solid #b3b3b3;
}

.category img {
  width: 10px;
}
/*# sourceMappingURL=Products.css.map */
.register-container, .login-container, .profile-container {
  width: 90%;
  max-width: 500px;
  display: block;
  margin: 0 auto;
  margin-top: 40px;
  border: 1px solid black;
  border-radius: 10px;
}

.error-text {
  text-align: center;
  font-size: 0.8rem;
  color: red;
  font-weight: 500;
}

.login-title {
  text-align: center;
  font-size: 1rem;
  margin: 15px;
}

.form-container {
  width: 90%;
  margin: 0 auto;
  padding-bottom: 20px;
}

.form {
  margin-bottom: 25px;
}

.form label {
  font-size: 0.9rem;
  margin-left: 2%;
}

.form input {
  width: 100%;
  padding: 5px;
  padding-left: 20px;
  border: none;
  border-bottom: 1px solid black;
}

.form input::-webkit-input-placeholder {
  font-size: 0.7rem;
  font-style: italic;
  color: #c2c2c2;
}

.form input:-ms-input-placeholder {
  font-size: 0.7rem;
  font-style: italic;
  color: #c2c2c2;
}

.form input::placeholder {
  font-size: 0.7rem;
  font-style: italic;
  color: #c2c2c2;
}

.main-btn {
  display: block;
  margin: 0 auto;
  width: 50%;
  padding: 5px;
  cursor: pointer;
}

.secondary-btn {
  font-size: 0.8rem;
  display: block;
  margin: 0 auto;
  padding: 2px;
  border: none;
  cursor: pointer;
  background: transparent;
}

.btn-left {
  margin: 20px 5%;
  border-bottom: 1px solid black;
}

.email-text {
  margin-left: 5%;
}
/*# sourceMappingURL=Login.css.map */
.back {
  width: 20px;
  margin: 20px 10%;
  display: block;
}

.cart-title {
  width: 95%;
  display: block;
  margin: 0 auto;
  font-weight: 500;
  border-bottom: 1px solid #b3b3b3;
}

.cart-item {
  width: 95%;
  max-width: 1000px;
  display: block;
  margin: 20px auto;
  border-radius: 5px;
  box-shadow: 0 0 2px #b3b3b3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 5%;
}

.cart-item-img {
  max-width: 40px;
  margin-right: 10px;
}

.cart-item-title {
  font-size: 0.75rem;
  font-weight: 500;
}

.cart-item-price {
  font-size: 0.6rem;
  font-weight: 500;
}

.img-section {
  width: 70%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.counter .decrement, .counter .increment {
  outline: none;
  cursor: pointer;
  border: 1px solid #b3b3b3;
  border-radius: 10px;
  background: none;
  width: 15px;
  margin: 3px;
  font-size: 0.6rem;
}

.counter .decrement:hover, .counter .decrement:focus, .counter .increment:hover, .counter .increment:focus {
  background: #b3b3b3;
}

.counter .count {
  font-size: 0.8rem;
}

.cart-btn {
  border: 1px solid #b3b3b3;
  border-radius: 2px;
  background: transparent;
  font-size: 1rem;
  font-weight: 500;
  padding: 5px 15px;
  display: block;
  margin: 15px auto;
  cursor: pointer;
}

.cart-btn:hover {
  background: #b3b3b3;
}

@media (min-width: 400px) {
  .cart-title {
    width: 80%;
  }
  .cart-item {
    width: 80%;
  }
  .cart-item-img {
    max-width: 80px;
    margin-right: 20px;
  }
  .counter .decrement, .counter .increment {
    margin: 3px;
    width: 20px;
  }
  .cart-item-price {
    font-size: 0.8rem;
  }
}
/*# sourceMappingURL=Basket.css.map */
